<template>
  <pagesBanner></pagesBanner>
  <section class="login pages position-relative overflow-hidden">
    <div class="container position-relative z-index-1 box_">
      <div class="bread-crumbs py-3 px-lg-5">
        <ol
          itemscope=""
          itemtype="http://schema.org/breadcrumbList"
          id="breadcrumbs"
        >
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <router-link to="/index" itemprop="item" title="劉記花生">
              <span itemprop="name">首頁</span>
            </router-link>
            <meta itemprop="position" content="1" />
          </li>
          <li
            itemprop="itemListElement"
            itemscope=""
            itemtype="http://schema.org/ListItem"
          >
            <span class="mx-2">/</span>
            <router-link to="/register" itemprop="item" title="會員註冊">
              <span itemprop="name">會員註冊</span>
            </router-link>
            <meta itemprop="position" content="2" />
          </li>
        </ol>
      </div>
      <div class="main-box bg-white px-lg-5 px-4 py-lg-5 py-4 mb-4">
        <div class="row justify-content-center">
          <div class="col-xl-6 col-lg-8 col-md-9 py-lg-4">
            <div class="inner_border px-lg-5 px-4 py-3">
              <div class="px-lg-3">
                <h3 class="pb-3 mb-3">會員註冊</h3>
                <div class="info mb-3">
                  請輸入您的電子信箱為登入帳號。請輸入與牢記您的密碼。按下註冊後系統將會發送認證信至您的信箱，請開啟信箱並點擊確認連結開通您的帳號。
                </div>
                <div>
                  <form class="form-group">
                    <div class="mb-md-4 mb-3 position-relative">
                      <input
                        name="Email"
                        id="Email"
                        type="text"
                        required
                        value=""
                      />
                      <label alt="Placeholder" placeholder="使用者信箱"></label>
                      <div class="icon_">
                        <img src="@/assets/img/user_.svg" alt="" />
                      </div>
                    </div>
                    <div class="mb-md-4 mb-3 position-relative">
                      <input
                        name="password"
                        id="password"
                        :type="passwordFieldType"
                        required
                        value=""
                      />
                      <label alt="Placeholder" placeholder="密碼"></label>
                      <div class="icon_">
                        <img src="@/assets/img/password_.svg" alt="" />
                      </div>
                      <a href=""
                        class="icon icon-eye btnPassword"
                        :class="{'show-password' : isSee}"
                        @click.prevent="seePwd"
                      ></a>
                    </div>
                    <div class="mb-md-4 mb-3 position-relative">
                      <input
                        name="password"
                        id="password"
                        :type="passwordFieldTypeCheck"
                        required
                        value=""
                      />
                      <label alt="Placeholder" placeholder="確認密碼"></label>
                      <div class="icon_">
                        <img src="@/assets/img/password_.svg" alt="" />
                      </div>
                      <a href=""
                        class="icon icon-eye btnPassword"
                        :class="{'show-password' : isSeeCheck}"
                        @click.prevent="seePwdCheck"
                      ></a>
                    </div>
                    <div class="mb-4">
                      <div
                        class="g-recaptcha d-flex justify-content-center"
                        data-sitekey="6LeLxiEjAAAAAE-usht2BcO1jLgelpSTkvicaQ6o"
                        data-callback="verify"
                        data-expired-callback="expired"
                        data-error-callback="error"
                      ></div>
                    </div>
                    <div class="mb-4 text-center">
                      <button class="main_btn_o btn py-2 px-5" href="#">
                        確認註冊
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import pagesBanner from '../components/pagesBanner.vue'
export default {
  components: {
    pagesBanner
  },
  data () {
    return {
      isSee: false,
      passwordFieldType: 'password',
      isSeeCheck: false,
      passwordFieldTypeCheck: 'password'
    }
  },
  methods: {
    simpleScrollTrigger () {
      this.gsap.from('.box_', {
        y: 50,
        opacity: 0,
        duration: 1,
        delay: 0.5,
        scrollTrigger: {
          trigger: '.box_',
          start: 'top 90%'
        }
      })
    },
    seePwd () {
      this.isSee = !this.isSee
      this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password'
    },
    seePwdCheck () {
      this.isSeeCheck = !this.isSeeCheck
      this.passwordFieldTypeCheck = this.passwordFieldTypeCheck === 'password' ? 'text' : 'password'
    }
  },
  mounted () {
    this.simpleScrollTrigger()
  }
}
</script>
